@import "_mixins.scss";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header {
  background-color: black;
  height: 30px;
  color: white;
}

.container {
  margin: 0 auto;
  width: 1200px;
}

#header_left {
  float: left;
  text-transform: uppercase;
}

#header_right ul li {
  float: left;
  margin-left: 20px;;
  text-transform: uppercase;
  list-style-type: none;
  cursor: pointer;
}


#header_right ul {
  float: right;
}


span {
  color: #23b99f;
  font-weight: bold;
}


.dropdown {
  position: relative;
  display: inline-block;
}

#dropdown_content {
  background-color: black;
  position: absolute;
  min-width: 100px;
  height: 100px;
  display: none;
}

.dropdown:hover #dropdown_content {
  display: block;
}
